$white: #fff;
$primaryColor: rgb(0, 119, 181);
$primaryColorHover: #0c8bcd;
$menuRadius: 7.5px;
.NavBar {
  background-color: $primaryColor;
  box-shadow: 0 4px 2px -2px gray;
  height: 50px;
  .logo {
    width: 60;
    margin-right: 15px;
    height: inherit;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      background: #fff;
      border-radius: 50%;
    }
  }
  .right-widget {
    float: right;
    height: inherit;
    width: 200px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .anticon-bell,
    .anticon-star {
      padding: 7.5px;
      cursor: pointer;
      font-size: 20px;
      &:hover {
        border-radius: $menuRadius;
        background: $primaryColorHover;
      }
    }
    .avatar-widget {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 35px;
      width: 60px;
      padding: 10px 5px;
      cursor: pointer;
      &:hover {
        border-radius: $menuRadius;
        background: $primaryColorHover;
      }
      .anticon-caret-down {
        font-size: 14px;
        margin-top: 3px;
        margin-right: 2px;
      }
    }
  }
  .ant-menu {
    line-height: 48px;
    background-color: $primaryColor;
    color: $white;
    border: none;

    .ant-menu-item {
      border: none;
      background: $primaryColor;
      padding: 0 3px;
      span {
        color: $white;
        padding: 8px 15px;
        border-radius: $menuRadius;
      }
    }

    .ant-menu-item-active {
      span {
        background: $primaryColorHover;
      }
    }

    .ant-menu-item-selected {
      span {
        color: $primaryColor;
        background: $white;
      }
    }

    .ant-menu-submenu {
      border: none;
      color: $white;
      .ant-menu-submenu-title {
        padding: 0 3px;
        span {
          padding: 8px 15px;
          border-radius: $menuRadius;
        }
      }
    }

    .ant-menu-submenu-active {
      .ant-menu-submenu-title {
        span {
          color: $white;
          background: $primaryColorHover;
        }
      }
    }
    .ant-menu-submenu-selected {
      .ant-menu-submenu-title {
        span {
          color: $primaryColor;
          background: $white;
        }
      }
    }
  }
}
